	module.exports = {"data":{"markdownRemark":{"html":"<p><strong><a href=\"/tag/doron\">SS “勇者ドロン顛末記 ” を見る (約2,000文字)</a></strong> 時は中世、魔王が世界を支配しようとしていた時代の話。世界中の国家が次々と魔王の侵攻に倒れていく中、一つの王国が耐え続けていた……しかし、その国の兵力も食料も限界に近づきつつあった。国王は悩んだ末、最後の切り札である勇者ドロンを召喚した…… ……こう書くと初代ドラクエ型の王道ファンタジーストーリーを思い浮かべるかもしれませんが、<em>ギャグ100%</em>です。この世界の生物は皆虫歯とは無縁です。</p>","frontmatter":{"cover":null,"date":"2002-10-01T00:10:01+09:00","strDate":"2002/9/30","title":"勇者ドロン顛末記"}}},"pathContext":{"path":"/ja/blog/2002/10/01-勇者ドロン顛末記/","langKey":"ja"}}

